
import React from "react";

// Accepts props `size:integer` (optional), `color:string` (optional).
function Square(props) {
    let wh = "24"
    if (props.size !== undefined) wh = props.size;
    return (
        <svg xmlns="http://www.w3.org/2000/svg" width={wh} height={wh} viewBox="0 0 24 24" fill="none"
             stroke="currentColor" strokeWidth="2" strokeLinecap="butt"
             strokeLinejoin="miter" className="feather feather-square">
            <line x1="1" y1="3" x2="23" y2="3" stroke="black"/>
            <line x1="1" y1="21" x2="23" y2="21" stroke="black"/>
            <line x1="1" y1="2" x2="1" y2="22" stroke="black"/>
            <line x1="22" y1="2" x2="22" y2="22" stroke="black"/>
        </svg>
    )
}

